.sidebarrow {
  display: flex;
  align-items: center;
  padding: 10px 20px;
}
.sidebarrow-icons {
  color: #606060;
  font-size: large !important;
}
.sidebarrow-title {
  flex: 1;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
}
.sidebarrow:hover {
  background-color: lightgray;
  cursor: pointer;
}

.sidebarrow:hover > .sidebarrow-icons {
  color: red;
}
.sidebarrow:hover > .sidebarrow-title {
  font-weight: bold;
}
.sidebarrow.selected{
    background-color: lightgray;
}
.sidebarrow.selected > .sidebarrow-icons{
    color: red;
}
.sidebarrow.selected > .sidebarrow-title{
    font-weight: bold;
}