.search-page{
    flex: 0.8;
    background-color: #f9f9f9;
    padding: 20px 20px;
}

.search-page-filter{
    display: flex;
    align-items: center;
    color: #606060;
    font-size: xx-smal !important;
}
.search-page-filter > h2 {
    margin-left: 10px;
    text-transform: uppercase;
}

.search-page > hr {
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}