.recommendedVideos{
    flex: 0.8;
    background-color: #f9f9f9;
    padding: 40px 20px;
    padding-bottom: 0;
}
@media (max-width:767px){
    .recommendedVideos{
        flex: 1; 
    }
}
.recommendedVideos > h2 {
    margin-left: 5px;
    margin-bottom: 20px;
}
.recommendedvideos-videos{
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
}

@media (max-width:767px){
    .recommendedvideos-videos{
        display: flex;
        align-items: center;
        justify-content: center;
        
    }
}