.videocard{
    margin-bottom: 40px;
    width: 270px;
}
.videocard-image{
    max-width: 250px;
    max-height: 140px;
}
.videocard-info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;

}
.video-text{
    margin-left: 15px;
}

.video-text > h4{
    font-size: 14px;
    margin-bottom: 5px;
}
.video-text > p{
    font-size: 14px;
    color: gray;
}
.video-avatar{
    height: 30px !important;
    width: 30px !important;
}