.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
}
.burgermenu {
  font-size: 20px;
}
.header-logo {
  height: 25px;
  object-fit: contain;
  margin-left: 20px;
}

.header-input {
  display: flex;
  align-items: center;
  min-height: 30px;
  min-width: 40%;
  border: 1px solid lightgray;
}

.header-input > input {
  flex: 1;
  border: none;
}

.header-input > input:focus {
  outline: none;
}
.header-input-button {
  min-width: 50px !important;
  min-height: 27px;
  background-color: #fafafa;
  border-left: 1px solid lightgray;
  color: gray;
}

.header-icons {
  display: flex;
  align-items: center;
}

.header-icon {
  margin-right: 8px;
}
.header-icons > img {
  border-radius: 50%;
  width: 25px !important;
  height: 25px !important;
}
